.app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: var(--background-color);
  color: var(--support-color);
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  padding-top: 80px;
}

.subpage {
  min-height: 80vh;
}

.std-shadow, .std-card {
  -webkit-box-shadow: 0px 0px 25px -6px var(--main-color);
          box-shadow: 0px 0px 25px -6px var(--main-color);
}

.std-card {
  border-radius: 20px;
}

.std-btn {
  position: relative;
  width: 100px;
  padding: 20px;
  border: 1px solid var(--outline-color);
  color: var(--outline-color);
  border-radius: 15px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  -webkit-box-shadow: 0px 0px 20px -6px var(--outline-color);
          box-shadow: 0px 0px 20px -6px var(--outline-color);
}

.std-btn a {
  text-decoration: none;
}

.std-btn:hover {
  cursor: pointer;
  color: var(--background-color);
  background-color: var(--outline-color);
}

.std-btn:active {
  -webkit-box-shadow: 2px 2px 10px 0px var(--outline-color), -2px -2px 10px 0px var(--outline-color);
          box-shadow: 2px 2px 10px 0px var(--outline-color), -2px -2px 10px 0px var(--outline-color);
}

button.std-btn {
  background-color: var(--background-color);
}
